 <template>
  <div>
    <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="()=>{
          $router.go(-1)
          }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">运维新增</span>
    </mu-appbar>
    <div class="body">
      <div class="flexBox backstyle">
        <div style="flex:2">
          <span>时间</span>
        </div>
        <div class="flexBox" style="flex:5" @click="()=>{
            show= true
            }">
          <div>
            <input class="input" type="text" placeholder="请选择时间" v-model="time" disabled />
          </div>
          <div class="imageSize flexBox">
            <img src="static/img/toBottom.png" alt />
          </div>
        </div>
      </div>
      <div class="flexBox backstyle">
        <div style="flex:2">
          <span>水站名称</span>
        </div>
        <div
          class="flexBox"
          style="flex:5"
          @click="()=>{
            showStation = true;
            }"
        >
          <div>
            <input class="input" type="text" placeholder="请选择站点" v-model="station" disabled />
          </div>
          <div class="imageSize flexBox">
            <img src="static/img/toBottom.png" alt />
          </div>
        </div>
      </div>
      <div class="flexBox backstyle">
        <div style="flex:2">
          <span>运维人员</span>
        </div>
        <div class="flexBox" style="flex:5">
          <div>
            <input class="input" type="text" placeholder="请输入运维人员" v-model="person" />
          </div>
          <!-- <div class="imageSize flexBox">
                   <img src="static/img/toBottom.png" alt="">
          </div>-->
        </div>
      </div>
      <div class="flexBox backstyle">
        <div style="flex:2">
          <span>事件名称</span>
        </div>
        <div class="flexBox" style="flex:5">
          <div>
            <input class="input" type="text" placeholder="请输入事件名称" v-model="title" />
          </div>
          <!-- <div class="imageSize flexBox">
                   <img src="static/img/toBottom.png" alt="">
          </div>-->
        </div>
      </div>

      <div class="flexBox backstyle">
        <div style="flex:2">
          <span>详情</span>
        </div>
        <!-- <div class="flexBox" style="flex:5">
          <div>
            <input class="input" type="text" placeholder="请输入问题详情" v-model="content" />
          </div>
          <div class=" flexBox">
                   <textarea name="" id="textarea" placeholder="请输入问题详情" v-model="content"></textarea>
          </div>
        </div>-->
      </div>
      <div class="flexBox">
        <textarea name id="textarea" placeholder="请输入事件详情" v-model="content"></textarea>
      </div>
      <div class="flexBox backstyle">
        <div style="flex:2">
          <span>图片</span>
        </div>
        <div class="flexBox" style="flex:5">
          <!-- <div>
                   <input class="input" type="text" placeholder="请选择时间" disabled >
               </div>
               <div class="imageSize flexBox">
                   <img src="static/img/toBottom.png" alt="">
          </div>-->
        </div>
      </div>
      <div class="backstyle2">
        <van-uploader v-model="fileList" multiple :after-read="afterRead" />
      </div>
    </div>
    <van-popup v-model="show" position="bottom">
      <van-datetime-picker
        v-model="currentDate"
        type="datetime"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="confirm"
        @cancel="()=>{
            show = false;
            time = ''
        }"
      />
    </van-popup>
    <van-popup v-model="showStation" position="bottom" style="z-index:99991">
      <van-picker
        :columns="columns"
        show-toolbar
        :close-on-click-overlay="false"
        title="选择站点"
        :default-index="0"
        @cancel="()=>{
            showStation = false
        }"
        @confirm="onChange"
      />
    </van-popup>
    <div class="flexBoxBottom">
      <div class="close" @click="()=>{
        $router.go(-1);
        }">
        <span>取消</span>
      </div>
      <div class="submit backgroundNaviBar" @click="submitAlert()">
        <span>提交</span>
      </div>
    </div>
    <van-overlay :show="showover" @click="showover = false" class="overlay">
      <van-loading type="spinner" color="#1989fa" />
    </van-overlay>
  </div>
</template>
<script>
import Vue from "vue";
import { Uploader } from "vant";
import AppBace from "../../../static/ajax";
Vue.use(Uploader);
import { Popup } from "vant";
import { Toast } from "vant";
import { Overlay } from "vant";
import { Loading } from "vant";
import { Dialog } from 'vant';
Vue.use(Loading);
Vue.use(Overlay);
Vue.use(Popup);
export default {
  data() {
    return {
      formdata: {},
      fileList: [],
      person: "",
      content: "",
      title: "",
      show: false,
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      time: "",
      station: "",
      showStation: false,
      columns: this.$store.state.menu,
      showover: false
    };
  },
  created() {
    this.token = sessionStorage.getItem("token");
    this.formdata = new FormData();
  },
  methods: {
    onChange(val) {
      this.station = val;
      this.showStation = false;
    },
    confirm(val) {
      this.time = AppBace.filterTime(val);
      this.show = false;
    },
    afterRead(file) {
      this.formdata.append("files", file.file);
    },
    submitAlert(){
      var _this = this;
      Dialog.confirm({
        // title: "提交表格",
        message: "确认提交？"
      })
        .then(() => {
          _this.showover = true;
          _this.submit();
        })
        .catch(() => {
          // on cancel
          Toast.fail("已取消");
        });
    },
    async submit() {
      // this.showover = true;
      var url = "/job/operate-record/problem";
      if (
        this.person == "" ||
        this.content == "" ||
        this.title == "" ||
        this.time == "" ||
        this.station == ""
      ) {
        Toast.fail("请先完善信息！");
        this.showover = false;
      } 
      else {
        this.formdata.append("person", this.person);
        this.formdata.append("content", this.content);
        this.formdata.append("title", this.title);
        this.formdata.append("time", this.time);
        this.formdata.append("station", this.station);

        var res = await AppBace.request(url, "POST", this.formdata, this.token);
        console.log(res);
        if (res.data.message === "success") {
          this.showover = false;
          Toast.success("上传成功");
          this.$router.go(-1);
        } else {
          this.showover = false;
          
          Toast.fail("失败，请重试");
        }
      }
    }
  }
};
</script>
<style  scoped>
.flexBox {
  display: flex;
  justify-content: space-between;
}
.imageSize img {
  width: 16px;
  height: 16px;
}
.imageSize {
  align-items: center;
}
.input {
  background-color: white;
  border: none;
}
.backstyle {
  background-color: white;
  height: 50px;
  align-items: center;
  border-bottom: 1px rgb(233, 232, 232) solid;
  padding: 0 10px;
}
.backstyle2 {
  background-color: white;

  align-items: center;
  border-bottom: 1px rgb(233, 232, 232) solid;
  padding: 0 10px;
  padding-top: 10px;
}
.flexBoxBottom {
  background-color: white;
  display: flex;
  justify-content: space-evenly;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 40px;
  align-items: center;
}
.close {
  border: 1px grey solid;
  color: grey;
  border-radius: 20px;
  padding: 2px 20px;
}
.submit {
  /* border: 1px blue solid; */
  /* background-color: blue; */
  color: white;
  padding: 2px 20px;
  border-radius: 20px;
}
.overlay {
  display: flex;
  justify-content: center;
  align-items: center;
}
#textarea {
  border: 1px solid rgb(233, 232, 232);
  padding: 10px;
  height: 100px;
  width: 100%;
  border-top: none;
}
</style>


